<template>
  <sf-modal-layout :title="title" :show="GuideBoardModal" :closeModal="closeModal" size="L">
		<el-row :gutter="30">
			<el-col :span="7">
				<!-- 구분 -->
				<sf-select-group
					:label="labels_('구분')"
					labelPosition="left"
					:labelSpan="5"
					:inputValue="GuideBoardModalValue.category"
					:onChange="value => setModalValue_({ key: 'category', value })"
					:options="categorys"
					:elProps="{ maxlength: 10 }"
				/>
			</el-col>
			<el-col :span="8">
				<!-- 상세구분 -->
				<sf-select-group
					:label="labels_('상세구분')"
					labelPosition="left"
					:labelSpan="9"
					:inputValue="GuideBoardModalValue.dtlCategory"
					:onChange="value => setModalValue_({ key: 'dtlCategory', value })"
					:options="selectedCategorys_(GuideBoardModalValue.category)"
					:elProps="{ maxlength: 10 }"
				/>
			</el-col>
			<el-col :span="8">
				<!-- 작성자 -->
				<sf-input-group
					v-if="GuideBoardModalValue.flg == 'I'"
					:label="labels_('작성자')"
					labelPosition="left"
					:labelSpan="8"
					:inputValue="GuideBoardModalValue.regId"
					:onChange="value => setModalValue_({ key: `regId`, value })"
					:elProps="{ maxlength: 5 }"
					placeholder="OOO"
				/>
				<sf-input-group
					v-if="GuideBoardModalValue.flg == 'U'"
					:label="labels_('작성자')"
					labelPosition="left"
					:labelSpan="8"
					:inputValue="GuideBoardModalValue.regId"
					:onChange="value => setModalValue_({ key: `regId`, value })"
					:elProps="{ maxlength: 5 }"
					placeholder="OOO"
					readonly
				/>
			</el-col>
		</el-row>
		<el-row :gutter="30">
			<el-col :span="24">
				<!-- 제목 -->
				<sf-input-group
					:label="labels_('신청항목명')"
					:inputValue="GuideBoardModalValue.name"
					:onChange="value => setModalValue_({ key: 'name', value })"
					:elProps="{ maxlength: 100 }"
				/>
			</el-col>
		</el-row>
		<el-row>
			<!-- 내용 -->
			<el-col :span="24">
				<sf-group-wrapper :label="labels_('내용')" :gutter="10" :labelSpan="5">
					<el-row :gutter="10">
						<el-col :span="24">
							<el-input
								id="lineHeight"
								type="textarea"
								v-model="GuideBoardModalValue.conts"
								maxlength="4000"
								:rows="10"
								show-word-limit
								@change="value => setModalValue_({ key: 'conts', value })"
							/>
						</el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
		</el-row>
		<template v-slot:footer>
			<sf-button line :onClick="closeModal">닫기</sf-button>
			<!-- <sf-button v-if="isButton_('AD') && GuideBoardModalValue.flg == 'U'" :onClick="saveAction">{{ getMessage_('SWCC000279') }}</sf-button>
			<sf-button v-if="isButton_('AD') && GuideBoardModalValue.flg == 'I'" :onClick="addAction">{{ getMessage_('SWCC000007') }}</sf-button> -->
      <sf-button v-if="GuideBoardModalValue.flg == 'U'" :onClick="saveAction">저장</sf-button>
			<sf-button v-if="GuideBoardModalValue.flg == 'I'" :onClick="addAction">추가</sf-button>
    </template>
	</sf-modal-layout>
</template>

<script>
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/SnackRegister/store/helpers';
import * as yup from 'yup';

export default {
  computed: {
    ...rootComputed,
    ...serviceComputed,

    schema() {
			return yup.object().shape({
				ver: yup
					.string()
					.required(this.$sf_formatString(this.getMessage_('SWCC000116'), [this.getMessage_('SWCC000330')])) //{0}은(는) 팔수 입력 값 입니다.
					.matches(/^[0-9]{1,2}.[0-9]{1,2}.[0-9]{1,2}$/, this.getMessage_('SWCC000949')), // 버전 양식에 맞게 작성해주세요.
				name: yup.string().required(this.$sf_formatString(this.getMessage_('SWCC000116'), [this.getMessage_('SWCC000519')])), //{0}은(는) 팔수 입력 값 입니다.
				category: yup.string().required(this.$sf_formatString(this.getMessage_('SWCC000116'), [this.getMessage_('SWCC000844')])), //{0}은(는) 팔수 입력 값 입니다.
				dtlCategory: yup.string().required(this.$sf_formatString(this.getMessage_('SWCC000116'), [this.getMessage_('SWCC000845')])) //{0}은(는) 팔수 입력 값 입니다.
			});
		}
  },
  methods: {
    ...rootMethods,
    ...serviceMethods,

    addAction() {
			try {
				// 유효성 검사
				// this.schema.validateSync(this.GuideBoardModalValue);

				this.$sf_showMessage({
					type: 'confirm',
					icon: 'confirm',
					// message: this.getMessage_('SWCC000503'), //추가하시겠습니까?
					message: '추가하시겠습니까?',
					buttons: [{ title: '추가', clickFn: () => this.add() }] //추가
				});
			} catch (error) {
				this.$sf_toast({ type: 'error', message: error.message });
			}
		},

    add() {
			const formData = new FormData();

			this.$sf_toggleLoading(true);
			Object.keys(this.GuideBoardModalValue).forEach(key => formData.append(key, this.GuideBoardModalValue[key]));
			// if (this.GuideBoardModalValue.fileList.length > 0)
			// 	this.GuideBoardModalValue.fileList.forEach(element => formData.append('fileList', element.raw));
			// else formData.append('fileList', '');

			this.addGuide_({ formData })
				.then(response => {
					if (response.result == "SUCCESS") {
						this.$sf_toast({
							type: 'ok',
							// message: this.getMessage_('SWCC000534')
							message: '등록 완료'
						});
						// this.fetchHistory_({ ...this.searchParams, currentPage: 1, pageUnit: this.pagingInfo.pageUnit });
						this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ })
						this.closeModal();
					} else {
						this.$sf_toast({ type: 'error', message: response.message });
					}
				})
				.catch(error => {
					this.$sf_toast({ type: 'error', message: error.message });
					Promise.reject(error);
				})
				.finally(() => {
					this.$sf_toggleLoading(false);
				});
		},

		saveAction() {
			try {
				// 유효성 검사
				// this.schema.validateSync(this.GuideBoardModalValue);

				// if (!this.GuideBoardModalValue.boardNo)
				// 	this.$sf_toast({
				// 		type: 'error',
				// 		message: this.$sf_formatString(this.getMessage_('SWCC000116'), [this.getMessage_('SWCC000360')])
				// 	});

				this.$sf_showMessage({
					type: 'confirm',
					icon: 'confirm',
					message: '수정하시겠습니까?', //수정하시겠습니까?
					buttons: [{ title: '수정', clickFn: () => this.save() }] //수정
				});
			} catch (error) {
				this.$sf_toast({ type: 'error', message: error.message });
			}
		},

		save() {
			const formData = new FormData();
			this.$sf_toggleLoading(true);
			Object.keys(this.GuideBoardModalValue).forEach(key => formData.append(key, this.GuideBoardModalValue[key]));

			// if (this.GuideBoardModalValue.fileList.length > 0)
			// 	this.GuideBoardModalValue.fileList.forEach(element => {
			// 		formData.append('fileList', element.raw);
			// 	});
			// else formData.append('fileList', '');

			this.updateRegistration_({ formData })
				.then(response => {
					if (response.result == "SUCCESS") {
						// this.$sf_toast({ type: 'ok', message: response.messageCd ? this.getMessage_(response.messageCd) : response.message });
						this.$sf_toast({ type: 'ok', message: '수정 완료'});
					} else if (response.result === "FAIL") {
						this.$sf_toast({ type: 'confirm', message: response.messageCd ? this.getMessage_(response.messageCd) : response.message });
					} else if (response.code === 3 || response.code === 4) {
						this.$sf_toast({ type: 'error', message: response.messageCd ? this.getMessage_(response.messageCd) : response.message });
					} else if (response.code === 5) {
						this.$sf_toast({ type: 'cancel', message: response.messageCd ? this.getMessage_(response.messageCd) : response.message });
					}
					// this.fetchHistory_({ ...this.searchParams, currentPage: 1, pageUnit: this.historyData.pageUnit });
					this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ })
					this.closeModal();
				})
				.catch(error => {
					this.$sf_toast({ type: 'error', message: error.message });
					Promise.reject(error);
				})
				.finally(() => {
					this.$sf_toggleLoading(false);
				});
		},

    closeModal() {
			this.closeModal_();
		},

  },
  data() {
    return {
      title : '신청 목록'
    }
  }
}
</script>

