<template>
	<sf-content-layout>
		<template v-slot:location>
			<sf-location :locations="locations" />
		</template>
		<template v-slot:title>
		  <sf-content-title title="신청" useFavorite :favorite="favorite" :onFavorite="onFavorite"/>
		</template>
		<template v-slot:titleEnd>
			<page-action />
		</template>
		<template v-slot:content>
			<main-content />
			<snack-register-modal />
			<snack-register-detail />
		</template>
	</sf-content-layout>
</template>

<script>
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/SnackRegister/store/helpers'; 
import PageAction from './Content/PageAction/PageAction';
import MainContent from './Content/MainContent';
import SnackRegisterModal from './Content/Modal/SnackRegisterModal';
import SnackRegisterDetail from './Content/Modal/SnackRegisterDetail';


export default {
  components: {
		PageAction,
		MainContent,
		SnackRegisterModal,
		SnackRegisterDetail
	},
  data() {
    return {
        title: 'Register',
        favorite: false
      }
  },
  computed: {
    ...rootComputed,
    ...serviceComputed,

    locations() {
      let result = ['차세대SF추진실', 'SF솔루션개발1팀', '신청페이지'];
      return result;
    },
  },
  methods: {
    onFavorite() {
      this.favorite = !this.favorite;
    }
  }
}
</script>

