import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';
import state from './state';

export const serviceComputed = {
	...mapState('snackRegisterStore', Object.keys(state)),
	...mapGetters('snackRegisterStore', Object.keys(getters))
};
export const serviceMethods = {
	...mapActions('snackRegisterStore', Object.keys(actions)),
	...mapMutations('snackRegisterStore', Object.keys(mutations))
};
