<template>
  <sf-data-table
      :columns="columns"
      :rows="rows"
		  :excel=true
      :customExcel="myExcelDown"
      index
      height="100%"
      paging
      :pageUnit="pageUnit_"
      :pageArray="screen.pageArray"
		  :currentPage="currentPage_"
      :totalSize="totalSize_"
      @changedPage="changedPage"
    >
  </sf-data-table>
</template>
<script>
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/SnackRegister/store/helpers';
import { MAIN_URL } from '@/constants';

export default {
	computed: {
		...rootComputed,
		...serviceComputed,

		columns() {
      return [
      {
        // label: this.getMessage_('SWCC000844'), //구분
        label: '구분',
        prop: 'category',
        minWidth: '80px'
      },
      {
        // label: this.getMessage_('SWCC000845'), //상세구분
        label: '상세구분',
        prop: 'dtlCategory',
        minWidth: '80px'
      },
      {
        // label: this.getMessage_('SWCC000519'), //제목
        label: '이름',
        prop: 'name',
        minWidth: '300px',
        clickEvent: this.clickEvent,
        highlight: true
      },
      {
        // label: this.getMessage_('SWCC000848'), //작성자
        label: '작성자',
        prop: 'regId',
        minWidth: '80px'
      },
      {
        // label: this.getMessage_('SWCC000849'), //작성일
        label: '작성일',
        prop: 'regDt',
        minWidth: '180px'
      }
			];
    },
		rows() {
			// return this.historyData.data;
			return this.historyData;
      // return this.historyData_.map(history => ({
			// 	...history
			// }));
		}
  },
	methods: {
		...rootMethods,
		...serviceMethods,

    changedPage({ currentPageVal, pageUnitVal }) {
      Promise.resolve()
      .then(() => 
      this.DEFAULT_ASSIGN({ key: 'pagingInfo', value: { ...this.pagingInfo, currentPage: currentPageVal, pageUnit: pageUnitVal }})
      )
      .then(this.findAllProcesses);
	  },

    findAllProcesses() {
			this.$sf_toggleLoading(true);

			this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ }).finally(() =>
				this.$sf_toggleLoading(false)
			);
		},

    myExcelDown() {
      // const url = 'http://localhost:8080/vtsv-fe/api/registrations/excelData';
      const urlSearchParams = this.urlSearchParams_;
      const url = `${MAIN_URL}/registrations/excelData?${urlSearchParams}`;
      const params = { url, columns: this.columns };
      this.$sf_xlsxDown(params).catch(error => {
				this.$sf_toast({ type: 'error', message: error.message });
			});
    },

    async clickEvent(row) {
			this.$sf_toggleLoading(true);

			this.setState_({ key: 'GuideBoardDetailValue', value: { ...this.GuideBoardDetailValue } });
			this.setDetailValue_({ key: 'boardNo', value: row.boardNo });
			this.setDetailValue_({ key: 'ver', value: row.ver });
			this.setDetailValue_({ key: 'category', value: row.category });
			this.setDetailValue_({ key: 'dtlCategory', value: row.dtlCategory });
			this.setDetailValue_({ key: 'name', value: row.name });
			this.setDetailValue_({ key: 'regId', value: row.regId });
			this.setDetailValue_({ key: 'conts', value: row.conts });
			this.setState_({ key: 'GuideBoardDetail', value: true });
			this.$sf_toggleLoading(false);
		}
  },
  created() {
		this.findAllProcesses();
	}
};
</script>
<style></style>
