<template>
  <sf-modal-layout :title="labels_('신청게시판')" :show="GuideBoardDetail" :closeModal="closeDetail" size="L">
		<el-row :gutter="30">
			<el-col :span="6">
				<!-- 구분 -->
				<sf-group-wrapper :label="labels_('구분')" labelPosition="left" :gutter="10" :labelSpan="12">
					<el-row :gutter="10">
						<el-col>
							{{ GuideBoardDetailValue.category }}
						</el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
			<el-col :span="7">
				<!-- 상세구분 -->
				<sf-group-wrapper :label="labels_('상세구분')" labelPosition="left" :gutter="10" :labelSpan="12">
					<el-row :gutter="10">
						<el-col>
							{{ GuideBoardDetailValue.dtlCategory }}
						</el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
			<el-col :span="6">
				<!-- 작성자 -->
				<sf-group-wrapper :label="labels_('작성자')" labelPosition="left" :gutter="10" :labelSpan="12">
					<el-row :gutter="10">
						<el-col>
							{{ GuideBoardDetailValue.regId }}
						</el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
		</el-row>
		<el-row>
			<!-- 제목 -->
			<el-col :span="24">
				<sf-group-wrapper :label="labels_('제목')" :gutter="10" :labelSpan="5">
					<el-row :gutter="10">
						<el-col :span="24">
							{{ GuideBoardDetailValue.name }}
						</el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
		</el-row>
		<el-row>
			<!-- 내용 -->
			<el-col :span="24">
				<sf-group-wrapper :label="labels_('내용')" :gutter="10" :labelSpan="5">
					<el-row :gutter="10">
						<el-col :span="24" v-html="nl2br(GuideBoardDetailValue.conts)"> </el-col>
					</el-row>
				</sf-group-wrapper>
			</el-col>
		</el-row>
		<template v-slot:footer>
			<sf-button line color="error" :onClick="deleteAction">삭제</sf-button>
			<sf-button :onClick="updateAction">수정</sf-button>
			<sf-button line :onClick="closeDetail">닫기</sf-button>
		</template>
	</sf-modal-layout>
</template>

<script>
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/SnackRegister/store/helpers';

export default {
  computed: {
		...rootComputed,
		...serviceComputed,
  },
  
  methods: {
    ...rootMethods,
    ...serviceMethods,

    deleteAction() {
			this.$sf_showMessage({
				type: 'confirm',
				icon: 'confirm',
				message: '삭제하시겠습니까?', //삭제하시겠습니까?,
        cancelLabel: '취소',
				buttons: [{ title: '삭제', clickFn: () => this.delete() }] //삭제
			});
		},

		delete() {
			this.$sf_toggleLoading(true);
			this.delGuide_({ params: { ...this.GuideBoardDetailValue } })
				.then(response => {
					this.$sf_toast({ type: 'ok', message: response.message });
					// this.fetchHistory_({ ...this.searchParams, currentPage: 1, pageUnit: this.historyData.pageUnit });
					this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ })
					this.closeDetail();
				})
				.catch(error => {
					this.$sf_toast({ type: 'error', message: error.message });
				})
				.finally(() => {
					this.$sf_toggleLoading(false);
				});
		},

		updateAction() {
			this.setState_({ key: 'GuideBoardModalValue', value: { flg: 'U', ...this.GuideBoardDetailValue } });
			this.setState_({ key: 'GuideBoardDetail', value: false });
			this.setState_({ key: 'GuideBoardModal', value: true });
		},

		closeDetail() {
			this.closeDetail_();
		},

    nl2br(str) {
			return str.replace(/(?:\r\n|\r|\n)/g, '<br />');
		},

  }
  
}
</script>

<style>
span.caret-wrapper {
	display: none;
}
</style>